<template>
	<div class="test">
   <!--  <hello></hello> -->
    <h1>{{ title }}</h1>
    <h1 v-html='title'></h1>
    <h1 v-text='title'></h1>
    <input type='text' v-model='title'>
    <input type='checkbox' v-model='checked'>
    <p v-for='(value,index) in nav' v-bind:title='value' 
     :key='value'
     :class='{ on: index == curIndex }'
     @click='show(index)'>
    {{ index }}-{{ value }}
    </p>
    <button @click=' isShow =! isShow '>toggle</button>
    <p v-show='isShow'>show</p>
    <p v-if='isShow'>if</p>
    <p v-else-if='!isShow'>ss</p>
    <!-- <p v-else>else</p> -->
    <!-- <p :style="{'margin-left':dbCurNum}">haha</p> -->
    <test toutou='任务清单' @hi='hi'>
    	<h1>hahahahahha</h1>
    </test>
	</div>
</template>>

<script>
 import test1 from '~/test1'
 export default {
     data(){
    return{
      title:'<h2>德玛西亚</h2>',
      nav:['首页','产品','联系'],   
      curIndex:0 ,
      isShow:false,
      checked:false
    }
  },
  components:{
  //   hello:a
       test:test1

   },
  methods:{
    show(index){
      this.curIndex=index
    },
    hi (data) {
    	alert('触发事件')
    }
  }
 }
	
</script>

<style scoped>
	.on{
  font-size: 20px;
 }
 [v-cloak]{
  display:none;
 }
</style>


