<template>
	<div class="test">
		<slot></slot>
		<!-- 按回车执行addTask ：添加输入文在到tasks数组 -->
		<input type="text"  @keydown.enter='addTask'>
		<!-- 循环tasks数组对象 -->
		<p v-for='(item,index) in tasks'>
			<input type="checkbox" @click='item.checked = !item.checked'>
			<span :class='{isfinish: item.checked}'>{{ item.content }}</span>
			<button @click='remove(index)'>删除</button>
		</p>
		<h1 v-if='!tasks.length'>你已经完成任务</h1>
		<h1>{{toutou}}</h1>
	</div>
</template>

<script>
    export default {
	  props:['toutou'],
	  created () {
        if(localStorage.getItem('tasks')){
        	var data = localStorage.getItem('tasks')
      		this.tasks = JSON.parse(data)
        }
        
	  },
      data(){
        return{
        	tasks: []
        }
      },
      methods:{
      	addTask (e){
      		var task = {
      			content: e.target.value, //将表单的值复制给content对象
      			checked: false //根据checked的值显示是否完成
      		}
      		e.target.value='' //清空表单
      		this.tasks.push(task) //将task对象添加到tasks数组中
      		var data = JSON.stringify(this.tasks)
      		localStorage.setItem('tasks',data)
      		this.$emit('hi',this.tasks)
      	},
      	remove(index){
      		this.tasks.splice(index,1) //使用数组solice方法来删除对应的任务
      		var data = JSON.stringify(this.tasks)
      		localStorage.setItem('tasks',data)
      	}
      }              
	}
</script>

<style scoped>
	.isfinish{
		text-decoration: line-through;
		color: #ccc;
	}
</style>


