import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import test from '~/test'
import test1 from '~/test1'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      alias:'/index',
      name: 'Hello',
      component: Hello
    },
    {
    	path: '/test',
    	name: 'test',
    	component: test
    },
    {
        path: '/test1',
        name: 'test1',
        component: test1
    }
  ]
})



